import { Component, OnInit } from '@angular/core';
import { VhAuth } from 'ionic-vhframeworks';

@Component({
  selector: 'app-user-guide',
  templateUrl: './user-guide.component.html',
  styleUrls: ['./user-guide.component.scss']
})
export class UserGuideComponent implements OnInit {
  isFirstTime:any
  constructor( public vhAuth: VhAuth) { }

  ngOnInit(): void {
    let isFirstTime = this.vhAuth.localStorageGET("isFirstTime_management")
    if(isFirstTime) this.isFirstTime = false
    else this.isFirstTime = true
  }

  done(){
    this.isFirstTime = false
    this.vhAuth.localStorageSET("isFirstTime_management", true)
  }

}
